import React from 'react'
import {BrowserRouter as Router ,Route,Link} from 'react-router-dom'
import Main from '../containers/Main'

const router = (
    <Router>
          <div>
            <Route exact path="/" component={Main} />
          </div>
    </Router>
)

export default router


